<template>
    <div class="icons-box">
        <!-- 确保pages有数据才渲染 -->
        <swiper v-if="pages.length > 1" :options="swiperOption">
            <swiper-slide class="icons" v-for="(page, index) in pages" :key="index">
                <!-- 二级遍历 -->
                <div v-for="icon in page" :key="icon.id" class="icon">
                    <div class="icon-img-box">
                        <img class="icon-img" :src="icon.iconUrl" />
                    </div>
                    <p class="icon-desc">{{ icon.iconDesc }}</p>
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name : 'HomeIcons',
    props : {
        iconsList : {
            type : Array,
            required : true
        }
    },
    data() {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                },
            }
        }
    },
    computed : {
        pages() {
            const pages = []
            this.iconsList.forEach( (item, index) => {
                // 八个图标为一个轮播页
                const page = Math.floor(index / 8)  // 0-7 -> page : 0  8-15 -> page : 1
                if (!pages[page]) {
                    pages[page] = []
                }
                pages[page].push(item)
            })
            // pages : [[item, item ... , item], [item, ...]]
            return pages
        }
    }
};
</script>

<style scoped lang="stylus">
    .icons-box >>> .swiper-container
        height 0
        width 100%
        padding-bottom 47%
    
    .icons
        height 0
        width 100%
        padding-bottom 40%
        overflow hidden

        .icon
            position relative
            float left
            width 25%
            height 0
            padding-bottom 20%

            .icon-img-box
                position absolute
                left 50%
                top 5%
                width 55%
                height 55%
                transform translateX(-50%)

                .icon-img
                    width 100%
                
            .icon-desc
                position absolute 
                left 50%
                bottom 5%
                width 100%
                transform translateX(-50%)
                text-align center 

</style>


