<template> 
    <div class="city-alphabet">
        <ul class="alphabet-list" v-if="myAlphabet.length != 0">
            <li class="item" 
                v-for="alp in myAlphabet" 
                :key="alp"
                :ref="alp"
                @click="bus.$emit('change', alp)"
                @touchstart="handleTouchStart"
                @touchmove="handleTouchMove"
                @touchend="handleTouchEnd"
            >{{ alp }}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name : 'CityAlphabet',
    data () {
        return {
            flag : false,
            startHeight : 0,
            timer : null
        }
    },
    props : {
        myAlphabet : {
            type : Array,
            required : true
        }
    },
    updated() {
        // 当父组件的数据被 props接收完成且页面渲染完成时触发, 设置初始滑动高度
        this.startHeight = this.$refs['A'][0].offsetTop
    },
    methods : {
        handleTouchStart() {
            this.flag = true
        },
        handleTouchMove(e) {
            if (this.timer) {
                clearTimeout(this.timer)
            }

            // 防抖,减少事件的触发
            this.timer = setTimeout( () => {
                if (this.flag) {
                    var moveHeight = e.touches[0].clientY,
                        index = Math.floor((moveHeight - this.startHeight - 82) / 22)
                    
                    // 防止滑出字母表
                    if (index >= 0 && index < this.myAlphabet.length) {
                        // 把目标字母储存到change事件中
                        this.bus.$emit('change', this.myAlphabet[index])
                    }
                }
            }, 16)
        },
        handleTouchEnd() {
            this.flag = false
        }
    }
};
</script>
 
<style scoped lang='stylus'>
    @import '~styles/varible.styl'
    .alphabet-list
        display flex
        flex-flow nowrap column
        justify-content center 
        position absolute 
        right 0
        top 1.65rem
        bottom 0
        width .4rem

        .item
            text-align center
            line-height .44rem
            color $bgColor
</style>


