<template>
    <div class="home-header">
        <div class="header-back">
            <div class="iconfont">&#xe624;</div>
        </div>
        <div class="header-inpArea">
            <div class="iconfont icon-search">&#xe632;</div>
            <input type="text" class="header-input" placeholder="找工作,找老婆点这">
        </div>
        <router-link :to="{ name : 'cityLink'}">
            <div class="header-city">
                {{ this.$store.state.city }}<span class="iconfont">&#xe6aa;</span>
            </div>
        </router-link>
    </div>
</template>

<script>
export default {
    name : 'HomeHeader',
    props : {
        position: {
            type : String,
            required : true
        }
    }
};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible'
    .home-header
        display flex
        align-items center
        line-height .86rem
        background-color $bgColor
        color #f0f0f0

        .header-back
            width .64rem
            text-align center

        .header-inpArea
            flex 1
            position relative
            display flex

            .icon-search
                position absolute
                left .4rem
                top 50%
                transform translateY(-50%)

            .header-input
                flex 1
                height .6rem
                line-height .6rem
                border none
                outline none
                border-radius .15rem
                box-sizing border-box
                padding 0 0 0 .6rem
                margin-left .2rem
            

        .header-city
            width 1.24rem
            text-align center
            color #f0f0f0
</style>


