<template>
    <div class="home-weekend">
        <div class="weekend-title">
            <p class="title">周末去哪儿</p>
        </div>
        <div class="weekend-content">
            <div class="weekend-recomment" v-for="item in weekendList" :key="item.id">
                <div class="weekend-imgArea">
                    <img class="weekend-img" :src="item.imgUrl" alt="">
                </div>
                <div class="weekend-desc">
                    <p class="desc-title">{{ item.desc[0] }}</p>
                    <p class="desc">{{ item.desc[1] }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'HomeWeekendLike',
    props : {
        weekendList : {
            type : Array,
            required : true
        }
    }
};
</script>

<style scoped lang='stylus'>
    .home-weekend
        overflow hidden

        .weekend-title
            height .8rem
            width 100%
            background-color rgb(245, 245, 245)

            .title
                line-height .8rem
                font-size .28rem
                box-sizing border-box
                padding-left .2rem
                color #000

        .weekend-content
            overflow hidden

            .weekend-recomment
                height 4.3rem

                .weekend-imgArea
                    height 3rem

                    .weekend-img
                        width 100%
                        height  100%

                .weekend-desc
                    display flex
                    height 1.3rem
                    flex-flow nowrap column
                    justify-content center
                    box-sizing border-box
                    padding-left .3rem 

                    .desc-title
                        font-size .35rem
                        margin-bottom .1rem

                    .desc
                        font-size .25rem


</style>


