<template>
    <div class="city-header">
        <div class="header-content">
            <p class="header-title">
                城市选择
            </p>
            <router-link :to="{ name : 'homeLink'}">
                <div class="iconfont header-icon">&#xe624;</div>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    nmae : 'CityHeader',

};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible.styl'

    .city-header
        background-color $bgColor
        width 100%
        height .86rem

        .header-content
            position relative
            width 100%
            height 100%
            line-height .86rem
        
            .header-title
                color #f0f0f0
                text-align center
                font-size .3rem
                font-weight bold

            .header-icon
                position absolute
                left .2rem
                top 50%
                transform translateY(-50%)
                color #f0f0f0

</style>


