<template>
    <div class="city-list" ref="wrapper">
        <div v-if="myCities != null">
            <div class="list-area">
                <div class="list-title border-topbottom">当前城市</div>
                <div class="btn-area">
                    <div class="btn-wrapper">
                        <div class="btn">{{ this.$store.state.city }}</div>
                    </div>
                </div>
            </div>
            <div class="list-area">
                <div class="list-title border-topbottom">热门城市</div>
                <div class="btn-area">
                    <div 
                        class="btn-wrapper" 
                        v-for="hotCity in myCities.hotCities" 
                        :key="hotCity.id"
                        @click="handleCityClick(hotCity.name)"
                    >
                        <div class="btn">{{ hotCity.name }}</div>
                    </div>
                </div>
            </div>
            <div class="list-area" 
                v-for="(cities, alp) in myCities.cities" 
                :key="alp"
                :ref="alp"
            >
                <div class="list-title border-topbottom">{{alp}}</div>
                <div class="item-list">
                    <div 
                        class="item border-bottom" 
                        v-for="city in cities" 
                        :key="city.id"
                        @click="handleCityClick(city.name)"
                    >{{ city.name }}</div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name : 'CityList',
    props : {
        myCities : {
            type : Object,
            required : true
        }
    },
    data () {
        return {
            letter : null
        }
    }, 
    watch : {
        // 监听字母变化,得到对应的dom元素,并切换到指定的dom元素
        letter () {
            let element = this.$refs[this.letter][0]
            this.scroll.scrollToElement(element)
        }
    },
    mounted() {
        // 初始化滑动
        this.scroll = new BScroll(this.$refs.wrapper)
        // 获取字母表的字母
        this.bus.$on('change', data => {
            this.letter = data
        })
    },
    methods : {
        handleCityClick (city) {
            // 获取vuex的数据,并跳转至首页
            this.$store.dispatch('changeCity', city)
            this.$router.push({name : 'homeLink'})
        }
    }
};
</script>

<style scoped lang='stylus'>
    .border-topbottom
        &:before
            background-color #ccc
        &:after
            background-color #ccc
    
    .city-list
        position absolute
        top 1.65rem
        left 0
        right 0
        bottom 0
        overflow hidden

        .list-area
            .list-title
                line-height .5rem
                box-sizing border-box
                padding-left .2rem
                background-color #eee
                color #333
            
            .btn-area
                overflow hidden
                padding .1rem .6rem .1rem .1rem

                .btn-wrapper
                    float left
                    width 33.33%
                    
                    .btn 
                        text-align center
                        line-height .5rem
                        margin .1rem
                        border .02rem solid #777
                        border-radius .1rem
                        
            .item-list
                .border-bottom
                    &:before
                        background-color #ccc
                .item
                    line-height .65rem     
                    padding-left .2rem  
                    color #333
                    font-size .3rem   
</style>


