<template>
    <div class="home-recommend">
        <span class="recommend-content" v-for="(item, index) in recommendList" :key="item.id">
            <img class="recommend-img" :src="item.imgUrl">
        </span>
    </div>
</template>

<script>
export default {
    name : 'HomeRecommend',
    props : {
        recommendList : {
            type : Array,
            required : true
        }
    }
};
</script>

<style scoped lang="stylus">
    .home-recommend
        display flex
        justify-content space-between
        height 1.8rem
        box-sizing border-box
        border-top 10px solid rgb(245, 245, 245)
        background-color rgb(245, 245, 245)

        .recommend-content
            flex none 
            height 100%
            width 49.9%
            text-align center
            background-color #fff

            .recommend-img
                width 80%
                height 100%

</style>


