<template>
    <div>
        <div class="city-search">
            <input v-model="keyWord" type="text" placeholder="搜索城市" class="city-input">
        </div>
        <div class="search-content" ref="wrapper" v-show="keyWord">
            <ul class="search-list">
                <li 
                    class="search-item" 
                    v-for="city in list" 
                    :key="city.id"
                    @click="handleCityClick(city.name)"
                >{{ city.name }}</li>
                <li class="search-item" v-show="emptyList">没有找到匹配项</li>
            </ul>
        </div>
    </div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
    name : 'CitySearch',
    props : {
        cities : {
            type : Object,
            required : true
        }
    },
    computed : {
        // 判断list是否为空
        emptyList () {
            return this.list.length == 0
        }
    },
    data () {
        return {
            list : [],
            keyWord : '',
            timer : null
        }
    },
    watch : {
        keyWord () {
            if (this.timer) {
                clearTimeout(this.timer)
            }
            // 添加防抖, 减少事件触发
            this.timer = setTimeout( () => {
                const result = []
                for (let i in this.cities) {
                    this.cities[i].forEach( city => {
                        if (city.name.indexOf(this.keyWord) != -1 || city.spell.indexOf(this.keyWord) != -1) {
                            result.push(city)
                        }
                    });
                }
                this.list = result
            }, 300);
        }
    },
    mounted() {
        this.scroll = new Bscroll(this.$refs.wrapper)
    },
    methods : {
        handleCityClick (city) {
            // 获取vuex的数据,并跳转至首页
            this.$store.dispatch('changeCity', city)
            this.$router.push({name : 'homeLink'})
        }
    }
};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible.styl'
    .city-search
        width 100%
        height .8rem
        line-height .7rem
        background-color $bgColor
        box-sizing border-box
        padding 0 .4rem

        .city-input
            width 100%
            height .6rem
            border none 
            outline none
            border-radius .05rem
            box-sizing border-box
            padding 0 .4rem

    .search-content
        overflow hidden
        position absolute
        top 1.68rem
        left 0
        right 0
        bottom 0
        background-color #eee
        z-index 1

        .search-item
            line-height .6rem
            padding-left .4rem
            background-color #fff
            margin-bottom .02rem
</style>


