<template>
    <div class="home-likes">
        <div class="likes-title">
            <div class="title-imgArea">
                <img class="title-img" src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" alt="">
            </div>
            猜你喜欢
        </div>
        <div class="likes">
            <ul class="like-wrapper">
                <li class="like" v-for="likeItem in likeImgList" :key="likeItem.id">
                    <div class="like-imgArea">
                        <div class="like-use" v-if="likeItem.useFast">
                            <span class="use-img" :style="{background : 'url(' + likeItem.useUrl + ')'}">
                                随买随用
                            </span>
                        </div>
                        <img class="like-img" :src="likeItem.imgUrl" alt="">
                    </div>
                    <div class="like-content">
                        <div class="like-title">{{ likeItem.desc }}</div>
                        <div class="like-comment">
                            <!-- 通过每个like的star属性改变bgPosition改变星星的数量 -->
                            <span class="comment-img" :style="{ backgroundPosition : likeItem.star + 'px' }"></span>
                            {{ likeItem.comment }}条评论
                        </div>
                        <div class="like-price">
                            <em class="price-symbol">¥</em><em class="price">{{ likeItem.price }}</em>起
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class="more-list">
            查看所有产品
        </div>
    </div>
</template>

<script>
export default {
    name : 'HomeGuessULike',
    props : {
        likeData : {
            type : Object,
            required : true
        }
    },
    computed : {
        likeImgList() {
            return this.likeData['guessYouLikeImgList']
        }
    }
};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible.styl'
    .home-likes
        width 100%
        overflow hidden

        .likes-title
            width 100
            height .9rem
            box-sizing border-box
            padding .2rem 0 .2rem .1rem
            font-size .3rem
            font-weight bold

            .title-imgArea
                display inline-block         
                width .3rem
                height .3rem

                .title-img
                    width 100%   

        .likes
            width 100%
            overflow hidden

            .like-wrapper
                width 100%

                .like
                    width 100%
                    height 2rem
                    padding .2rem 0

                    .like-imgArea
                        position relative
                        float left
                        width 2rem
                        height 2rem
                        margin-left .2rem

                        .like-use
                            position absolute
                            left 0
                            top 0
                            width 1rem
                            height .4rem
                            color #f0f0f0
                            font-size .15rem 
                            line-height .4rem
                            
                            .use-img
                                display inline-block
                                width 100%
                                height 100%    
                                border-bottom-right-radius .3rem
                                box-sizing border-box
                                padding-left .05rem

                        .like-img
                            width 100%

                        &:after
                            display block
                            content ''
                            clear both
                            height 0

                    .like-content
                        display flex
                        height 2rem
                        flex-flow nowrap column
                        justify-content space-around
                        margin-left 2.4rem

                        .like-title
                            font-size .35rem
                        .like-comment
                            font-size .25rem 

                            .comment-img
                                display inline-block
                                width 1rem
                                height .2rem
                                margin-right .1rem
                                background url('https://img3.doubanio.com/f/shire/680a4bc4c384199245b080c7104da5be8ed717d3/pics/rating_icons/ic_rating_m.png') no-repeat
                                background-size cover
                                
                            
                        .like-price
                            font-size .25rem

                            .price-symbol
                                color #ff8300

                            .price
                                color #ff8300
                                font-size .4rem 
                                margin-right .05rem

        .more-list
            width 100%
            height .4rem
            padding .2rem 0
            text-align center
            line-height .4rem
            color $bgColor
            border-top .01rem solid #ddd

</style>


