<template>
    <div class="home-hotList">
        <div class="hotList-title">
            <div class="title-left">
                <div class="title-imgArea">
                    <img class="title-img" src="http://img1.qunarzz.com/piao/fusion/1711/16/bfbb9874e8f11402.png">
                </div>
                <span class="title-content">本周热门榜单</span>
            </div>
            <div class="title-right">
                <span class="all-list">全部榜单</span>
                <span class="iconfont">&#xe6f8;</span>
            </div>
        </div>
        <div class="list-box" ref="wrapper">
            <ul v-if="hostList.length > 1" class="hotList-list" :style="widthList">
                <li class="list" v-for="listItem in hostList" :key="listItem.id">
                    <div class="list-imgArea">
                        <div class="top-list" v-if="listItem.isTop">
                            <img class="top-img" :src="listItem.topUrl" alt="">
                        </div>
                        <img :src="listItem.imgUrl" alt="" class="list-img">
                    </div>
                    <p class="list-title">{{listItem.desc}}</p>
                    <p class="list-price">
                        <em class="list-priceNum">¥{{listItem.price}}</em>起
                    </p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name : 'HomeHotList',
    props : {
        hostList : {
            type : Array,
            required : true
        }
    },
    computed : {
        // 动态生成间隔
        widthList() {
            if (this.hostList.length > 1) {
                return {
                    width : (this.hostList.length * (100 + 11) + 11) + 'px'
                }
            }
        }
    },
    updated() {
        // 当父组件的数据传送到 props后再绑定 scroll,避免 wrapper下一层不存在的问题
        this.scroll = new BScroll(this.$refs.wrapper, {
            scrollX: true,
            scrollY: false,
        })
    },
};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible.styl'
    .home-hotList
        width 100%
        height 4rem
        border-top 10px solid rgb(245, 245, 245)
        border-bottom 10px solid rgb(245, 245, 245)
        overflow hidden

        .hotList-title
            height .44rem
            padding .24rem .24rem

            .title-left
                float left
                line-height .3rem
                font-size .3rem
                font-weight bold 
               
                .title-imgArea
                    display inline-block
                    width .3rem
                    height .3rem

                    .title-img
                        width 100%

            .title-right
                float right
                line-height .35rem
                font-size .25rem
                font-weight 600

        .list-box
            overflow hidden
            position absolute
            top 9rem
            left 0
            right 0
            bottom 0
        
            .hotList-list
                height 3.02rem

                .list
                    float left
                    width 2rem
                    margin-left .22rem

                    .list-imgArea
                        position relative
                        width 2rem
                        height 2rem

                        .top-list
                            position absolute
                            top 0
                            left 0
                            width .8rem
                            height .4rem

                            .top-img
                                width 100%

                        .list-img
                            width 100%

                    .list-title, .list-price
                        text-align center
                        font-weight bold
                        textWarp()
                        font-size .25rem
                        margin-top .05rem

                        .list-priceNum
                            color #ff8300
                            margin-right .05rem

</style>


